$border_header: 1px solid rgba(0, 0, 0, 0.2);
header {
    border: $border_header;

    .custom_logo{
        .logo_link{
            font-size: 1.5rem;
            text-decoration: underline;
            text-decoration-color: #F98699;

        }
      
    }
    @media screen and (max-width: 600px){
        .custom_logo{
            display: none!important;
        }
    }
   @media screen and (max-width: 992px){
    .sm_mobile_menu{
     display: block!important; 
     position: absolute;
     width: 100%;
     top: 100%;
     right: 0;  
     background: rgba(0, 0, 0, 0.801);
     z-index: 3333;
     color: #f7f7f7;
     margin: 0!important;
     padding: 0!important;
     max-width: 100%;
     .menu_sm{
         flex-direction: column;
         .menu_link{
             padding: 10px!important;
             display: block;
             color: #222;
             border: 1px solid rgba(0, 0, 0, 0.068);
             text-align: center;
            
         }
         li {
             a{
                 transition: .3s;
               &:hover{
                 background: rgba(0, 0, 0, 0.801);
                 transform: scaleX(.9) skewX(2deg)
               }

             }

            
         }

     }
     &.before_toggle_col{
         opacity: 0;
         transform: scale(0) ;
         border-radius: 100%;
         pointer-events: none;
         user-select: none;
     }
     &.after_toggle_col{
         opacity: 1;
         transform: scale(1);
         border-radius: 0%;
         pointer-events: visible;
         user-select: none;
     }
    }
    .toggle_nav{
        border: none;
        outline: none;
        box-shadow: 0 0 1px 1px #f8f8f8;
        width: 50px;
        padding: 3px;
        display: inline-block;
        background: #333;
    }
   }
}