// ......................Global Settings................ //
*{
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  transition: .5s;
  color: white;
  cursor:  url('./cursoo.cur'), auto;
  
}

body{
background: #000;

}
ul,ol{
  list-style: none;
  margin: 0;
  padding: 0;
}
a,a:hover{
  color: #f3f3f3;
  text-decoration: none;
}


// .....................custom color .....................// 
.color_gray{
  color: gray;
}
.color_pink{
  color: #F98699;
}
.color_white{
  color: white;
}

.particles{
  width: 100vw;
  height: 100vh;
  position: fixed;
  pointer-events: none;
  user-select: none;
  background: #000;
  z-index: -333333333333333333333333333;
}
