.homeSection1{
    position: relative;
    background: linear-gradient(rgba(34, 34, 34, 0.61), rgba(51, 51, 51, 0.767)), url('https://cdn.pixabay.com/photo/2015/01/15/12/46/model-600225__340.jpg') no-repeat fixed left top/cover;
    transition: 1s;
    .btn_wrapper{
        a{
            
            transition: .5s;
            border-radius: 5px;
            box-shadow: 2px 2px 6px inset white;
            &:hover{
                background: #888;
                color: #555
            }
        }
    }
    h1,h2{
        color: #444;
        text-shadow: 1px 1px #888,
                     -1px -1px #000;
    }
}