body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;font-family:Arial, Helvetica, sans-serif;-webkit-transition:.5s;transition:.5s;color:white;cursor:url(../../static/media/cursoo.6023b7fd.cur),auto}body{background:#000}ul,ol{list-style:none;margin:0;padding:0}a,a:hover{color:#f3f3f3;text-decoration:none}.color_gray{color:gray}.color_pink{color:#F98699}.color_white{color:white}.particles{width:100vw;height:100vh;position:fixed;pointer-events:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;background:#000;z-index:-333333333333333333333333333}

header{border:1px solid rgba(0,0,0,0.2)}header .custom_logo .logo_link{font-size:1.5rem;text-decoration:underline;-webkit-text-decoration-color:#F98699;text-decoration-color:#F98699}@media screen and (max-width: 600px){header .custom_logo{display:none !important}}@media screen and (max-width: 992px){header .sm_mobile_menu{display:block !important;position:absolute;width:100%;top:100%;right:0;background:rgba(0,0,0,0.801);z-index:3333;color:#f7f7f7;margin:0 !important;padding:0 !important;max-width:100%}header .sm_mobile_menu .menu_sm{flex-direction:column}header .sm_mobile_menu .menu_sm .menu_link{padding:10px !important;display:block;color:#222;border:1px solid rgba(0,0,0,0.068);text-align:center}header .sm_mobile_menu .menu_sm li a{-webkit-transition:.3s;transition:.3s}header .sm_mobile_menu .menu_sm li a:hover{background:rgba(0,0,0,0.801);-webkit-transform:scaleX(0.9) skewX(2deg);transform:scaleX(0.9) skewX(2deg)}header .sm_mobile_menu.before_toggle_col{opacity:0;-webkit-transform:scale(0);transform:scale(0);border-radius:100%;pointer-events:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}header .sm_mobile_menu.after_toggle_col{opacity:1;-webkit-transform:scale(1);transform:scale(1);border-radius:0%;pointer-events:visible;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}header .toggle_nav{border:none;outline:none;box-shadow:0 0 1px 1px #f8f8f8;width:50px;padding:3px;display:inline-block;background:#333}}



.homeSection1{position:relative;background:-webkit-gradient(linear, left top, left bottom, from(rgba(34,34,34,0.61)), to(rgba(51,51,51,0.767))),url("https://cdn.pixabay.com/photo/2015/01/15/12/46/model-600225__340.jpg") no-repeat fixed left top/cover;background:linear-gradient(rgba(34,34,34,0.61), rgba(51,51,51,0.767)),url("https://cdn.pixabay.com/photo/2015/01/15/12/46/model-600225__340.jpg") no-repeat fixed left top/cover;-webkit-transition:1s;transition:1s}.homeSection1 .btn_wrapper a{-webkit-transition:.5s;transition:.5s;border-radius:5px;box-shadow:2px 2px 6px inset white}.homeSection1 .btn_wrapper a:hover{background:#888;color:#555}.homeSection1 h1,.homeSection1 h2{color:#444;text-shadow:1px 1px #888, -1px -1px #000}

.HomeSection2 .swiper_container1{width:100%;height:300px}.HomeSection2 .swiper-slide{text-align:center;font-size:18px;background:#fff;border:solid #777 2px;display:flex;justify-content:center;align-items:center;border-radius:10px;overflow:hidden}.HomeSection2 .swiper-slide img{width:100%;height:100%;object-fit:cover}.HomeSection2 .swiper-slide .name{-webkit-writing-mode:vertical-rl;-ms-writing-mode:tb-rl;writing-mode:vertical-rl;background:black;color:white;height:100%;right:0}

.swiper_container2{width:100%;padding-top:50px;padding-bottom:50px}.swiper_container2 .swiper-slide{background-position:center;background-size:cover;width:300px;height:300px;background:url("https://cdn.pixabay.com/photo/2018/03/06/22/57/portrait-3204843__340.jpg") center/cover no-repeat}.swiper_container2 .swiper-slide:nth-of-type(1){background:url("https://cdn.pixabay.com/photo/2017/08/06/15/13/woman-2593366__340.jpg") center/cover no-repeat}.swiper_container2 .swiper-slide:nth-of-type(2){background:url("https://cdn.pixabay.com/photo/2018/02/21/08/40/woman-3169726__340.jpg") center/cover no-repeat}.swiper_container2 .swiper-slide:nth-of-type(3){background:url("https://cdn.pixabay.com/photo/2016/11/22/10/47/girl-1848947__340.jpg") center/cover no-repeat}.swiper_container2 .swiper-slide:nth-of-type(4){background:url("https://cdn.pixabay.com/photo/2018/02/24/20/40/fashion-3179178__340.jpg") center/cover no-repeat}.swiper_container2 .swiper-slide:nth-of-type(5){background:url("https://cdn.pixabay.com/photo/2016/04/16/19/51/fashion-model-1333640__340.jpg") center/cover no-repeat}.swiper_container2 .swiper-slide:nth-of-type(6){background:url("https://cdn.pixabay.com/photo/2016/03/23/08/34/beautiful-1274361__340.jpg") center/cover no-repeat}.swiper_container2 .swiper-slide:nth-of-type(7){background:url("https://cdn.pixabay.com/photo/2016/11/29/13/14/attractive-1869761__340.jpg") center/cover no-repeat}.swiper_container2 .swiper-slide:nth-of-type(8){background:url("https://cdn.pixabay.com/photo/2015/03/26/09/42/woman-690118__340.jpg") center/cover no-repeat}.swiper_container2 .swiper-slide:nth-of-type(9){background:url("https://cdn.pixabay.com/photo/2015/08/02/22/38/girl-872149__340.jpg") center/cover no-repeat}.swiper_container2 .swiper-slide:nth-of-type(10){background:url("https://cdn.pixabay.com/photo/2016/11/29/04/50/blur-1867402__340.jpg") center/cover no-repeat}


.HomeSection5 .img_person{width:120px;height:120px;border-radius:100%;margin:auto}.HomeSection5 .img_person img{width:inherit;height:inherit;border-radius:50%;box-shadow:2px -6px 2px rgba(153,153,153,0.438)}.HomeSection5 .custom_card{box-shadow:1px -1px  3px inset #888;background:#111}

.social_icon{position:fixed;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);display:inline-block;z-index:333;margin:5px}.social_icon ul li a{margin:2px;display:inline-flex;justify-content:center;align-items:center;color:#000;width:35px;height:35px;padding:5px;background:#fff;border-radius:50%;-webkit-transition:.5s;transition:.5s;box-shadow:1px 2px 1px rgba(0,0,0,0.753)}.social_icon ul li a:hover{box-shadow:3px 4px 3px rgba(0,0,0,0.753)}

.loading_wrapper{position:fixed;top:0;left:0;width:100vw;height:100vh;background:#000;display:grid;place-items:center;-webkit-transition:2s;transition:2s}.loading_wrapper .item{width:120px;height:120px;border-top:2px solid #f8f8f8;border-bottom:2px solid #fff;box-shadow:1px 1px 2px inset white;border-radius:50%;-webkit-animation:1s linear loading infinite;animation:1s linear loading infinite}@-webkit-keyframes loading{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(1turn);transform:rotate(1turn)}}@keyframes loading{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(1turn);transform:rotate(1turn)}}

